      <template v-slot:activator="{ on }">
        <v-btn round color="primary lighten-1" dark v-on="on">ÂØ©Ê†∏ÂêçÂñÆ</v-btn>
      </template>
      <v-card>
        <v-card-title style="font-size:1.5rem">üôÜ‚Äç‚ôÇÔ∏èÈÅ∏ÊìáÈÄöÈÅéÂØ©Ê†∏ÁöÑÊàêÂì°</v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;">
          <!-- ÈÅ∏ÂñÆ -->
            <v-card class="center" v-if="waits.length == 0" flat>
              <v-card-title style="font-size:1.2rem">
                Ê≤íÊúâÊàêÂì°ÈúÄË¶ÅÂØ©Ê†∏üôÇ
