        <v-card-actions style="margin-left:auto">
          <v-btn color="primary darken-1" flat @click="dialog = false">ÂèñÊ∂à</v-btn>
          <v-btn v-if="waits.length != 0" color="primary darken-1" flat @click="dialog = false;submit()">Á¢∫ÂÆö</v-btn>
        </v-card-actions>
